import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, PrevidenciaComplementar } from '../../../../models/dtos/mfp/previdenciaComplementar';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface PrevidenciaComplementarEditViewProps extends EditViewProps<PrevidenciaComplementar> {}

const PrevidenciaComplementarEditView: FC<PrevidenciaComplementarEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const selectedItem = getSelectedItem(props);
  const formProps = useForm<PrevidenciaComplementar>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Manutenção de Informações da Previdência Complementar"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 8, validation: /[0-9]/ } }}
              isPrependLabel={true}
              id="codigo"
              label="Código"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 250 } }}
              isPrependLabel={true}
              id="razaoSocial"
              label="Razão Social"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              id="cnpj"
              label="CNPJ"
              type={InputType.CNPJ}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default PrevidenciaComplementarEditView;
